body {
  margin: 0;
  background: url(../img/table.jpg);
  font-size: 9px;
  font-family: sans-serif;
  text-align: center;
}

.touch,
.has-touch .no-touch {
  display: none;
}

.has-touch .touch {
  display: block;
}

.rules {
  position: absolute;
  left: 2em;
  text-align: left;
  font-size: 1.5em;
}

.table {
  margin: 0 auto;
  width: 73em;
  float: right;
  clear: both;
}
.table .burnt,
.table .discard,
.table .deck {
  float: left;
}
.table .burnt {
  margin-right: 12em;
}

.new-game {
  background: #c7254e;
  color: #fff;
  border: 0;
  border-radius: 1em;
  box-shadow: 1px 1px 5px #000;
  text-shadow: 1px 1px 5px #000;
}

.card:after, .card {
  display: block;
  width: 16em;
  height: 25em;
  border-radius: 1em;
  box-shadow: 0.1em 0.1em 0.5em rgba(0, 0, 0, 0.4);
  padding: 1em;
  box-sizing: border-box;
  font-family: sans-serif;
  transition: all 0.5s ease-in-out, 0.1s margin;
  position: relative;
  background: #fff none no-repeat 1em 1em;
  background-size: 14em 23em;
  transform-style: preserve-3d;
}

.card:hover {
  z-index: 1;
  margin: -1em 0 0 1em;
}
.card.chosen {
  z-index: 1;
  margin: -1em 0 0 1em;
  box-shadow: 0.1em 0.1em 3em #ffc000;
}
.burnt .card:hover, .discard .card:hover, .down .card:hover, .card.face-down:hover {
  z-index: 0;
  margin: 0;
}
