.card:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url(../img/back.png);
  transform: rotateY(180deg);
}
.card.face-down {
  transform: rotateY(180deg);
}

.discard::before,
.deck::before,
.burnt::before {
  font-size: 2em;
  display: block;
  border: 1px solid rgba(0, 0, 0, 0.2);
  width: 8em;
  height: 12.5em;
  border-radius: 1em;
  box-sizing: border-box;
  padding-top: 5.5em;
}

.discard::before {
  content: "Discard pile";
}

.deck::before {
  content: "Draw pile";
}

.burnt::before {
  content: "Burnt";
}

.pile {
