.players {
  clear: both;
}

.player {
  width: 50%;
  float: left;
}
.player::before {
  content: attr(data-player-name) " (" attr(data-player-type) ")";
  font-size: 2em;
  width: 100%;
  display: block;
}
.player.current::before {
  color: #f00;
  font-weight: bold;
}
.player .down {
  float: left;
}

.hand {
  clear: both;
  min-width: 16em;
  height: 25em;
  margin: 2em 2em 1em 1em;
  padding-right: 12em;
}
.hand .card {
  float: left;
  margin-right: -12em;
  margin-bottom: 2em;
}
.hand .card:hover, .player .hand .card.chosen {
  margin-right: 1em;
}
.hand .card.face-down:hover {
  margin-right: -12em;
}
.player .hand .card:hover + .card.chosen, .player .hand .card.chosen + .card:hover, .player .hand .card.chosen + .card.chosen {
  margin-left: -12em;
}

.overlay {
  height: 100vh;
  width: 100vw;
  position: absolute;
  background: rgba(0, 0, 0, 0.4);
  top: 0;
  left: 0;
  text-align: center;
  z-index: 2;
}
.overlay .hand {
  margin: 10em auto;
  display: inline-block;
}
.overlay .hand::before {
  display: none;
}
.overlay .hand .card:last-child:hover {
  margin-right: -12em;
}

@media screen and (max-width: 1280px) {
  body {
    font-size: 7px;
  }

  .table {
    margin: 2em;
  }
}
@media screen and (max-width: 1023px) {
  body {
    font-size: 6px;
  }
}
@media screen and (max-width: 767px) {
  body {
    font-size: 5px;
  }

  .table {
    margin: 3em;
  }

  .player .pile {
    width: 12.5vw;
  }
}
@media screen and (max-width: 567px) {
  body {
    font-size: 4px;
  }

  .table {
    margin-top: 35em;
  }

  .player .pile {
    width: 10vw;
  }
}
/* fireworks! From: https://codepen.io/yshlin/pen/ylDEk */
.pyro {
  background: rgba(0, 0, 0, 0.4);
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}
.pyro::before {
  display: block;
  color: #fff;
  font-size: 3em;
  text-shadow: 0 0 4px #fff;
  content: "Game over!";
  top: 12em;
  position: absolute;
  width: 100%;
}
.pyro::after {
  color: #fff;
  font-size: 2em;
  text-shadow: 0 0 4px #fff;
  content: attr(data-player-name) " wins!";
  position: absolute;
  top: 20em;
  left: 0;
  width: 100%;
}
.pyro > .before,
.pyro .pyro > .after {
  position: absolute;
  width: 5px;
  height: 5px;
  border-radius: 50%;
  box-shadow: 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff, 0 0 #fff;
  -moz-animation: 1s bang ease-out infinite backwards, 1s gravity ease-in infinite backwards, 5s position linear infinite backwards;
  -webkit-animation: 1s bang ease-out infinite backwards, 1s gravity ease-in infinite backwards, 5s position linear infinite backwards;
  -o-animation: 1s bang ease-out infinite backwards, 1s gravity ease-in infinite backwards, 5s position linear infinite backwards;
  -ms-animation: 1s bang ease-out infinite backwards, 1s gravity ease-in infinite backwards, 5s position linear infinite backwards;
  animation: 1s bang ease-out infinite backwards, 1s gravity ease-in infinite backwards, 5s position linear infinite backwards;
}
.pyro > .after {
  -moz-animation-delay: 1.25s, 1.25s, 1.25s;
  -webkit-animation-delay: 1.25s, 1.25s, 1.25s;
  -o-animation-delay: 1.25s, 1.25s, 1.25s;
  -ms-animation-delay: 1.25s, 1.25s, 1.25s;
  animation-delay: 1.25s, 1.25s, 1.25s;
  -moz-animation-duration: 1.25s, 1.25s, 6.25s;
  -webkit-animation-duration: 1.25s, 1.25s, 6.25s;
  -o-animation-duration: 1.25s, 1.25s, 6.25s;
  -ms-animation-duration: 1.25s, 1.25s, 6.25s;
  animation-duration: 1.25s, 1.25s, 6.25s;
}

